import Vue from 'vue';
import Router from 'vue-router';
import Index from '../components/Index';
import BadPathway from '../components/universal/BadPathway';
import PoolsAndSpas from '../components/pages/PoolsAndSpas';
import Supplies from '../components/pages/Supplies';
import Resources from '../components/pages/Resources';
import Services from '../components/pages/Services';
import FindAPool from '../components/pages/FindAPool';



Vue.use(Router)

const router = new Router({
		mode: 'history',
		routes: [
		{path: '/',
		name: 'Index',
		component: Index,
		meta: {title: 'Dealers and Distributors'}
	},
	{
		path: '/pools-and-spas',
		name: 'PoolsAndSpas',
		component: PoolsAndSpas,
		meta: {title: 'Pools and Spas'}
	},
	{
		path: '/supplies',
		name: 'Supplies',
		component: Supplies,
		meta: {title: 'Supplies'}
	},
	{
		path: '/resources',
		name: 'Resources',
		component: Resources,
		meta: {title: 'Resources'}
	},
	{
		path: '/services',
		name: 'Services',
		component: Services,
		meta: {title: 'Services'}
	},
	{
		path: '/find-a-pool',
		name: 'FindAPool',
		component: FindAPool,
		meta: {title: 'Find a Pool'}
	},
	{path: '*',
		name: 'BadPathway',
		component: BadPathway,
		meta: {title: 'Bad Pathway'}
	}
	]
});

router.beforeEach((to, from, next)=>{
	document.title = to.meta.title;
	next();
})
export default router;


