<template>
				<ul :class="[spot, 'common']">
					<li>{{ page }}</li>
					<li>Commercial Service <a href="#" title="Commercial Service"> <img class="icon-sm" src="../../assets/images/action-commercial-icon.png" alt="Commercial Service"/></a></li>
				</ul>
</template>

<script>
	export default {
		data () {
			return {
				page: 'Pool Pros'
			}
		},
		mounted(){
			let routeName = this.$route.meta.title;
			this.page = routeName ? routeName : 'Pool Pros';
		},
		props:['spot']
	}
</script>

<style scoped>
/* Common Styles */
ul.common{
	list-style: none;
	margin: 0
}
ul.common>li{
	display: inline;
	padding: 0 25px;
	color: #ffffff;
}

/* Unique TopNav Styles */
ul.head{
	width: 100%;
	text-align: right;
	padding: 10px 0;
}

 ul.head>li{
	padding: 0 20px;
	font-size: 13px;
}

ul.head>li:last-of-type{
	padding-left: 20px;
	padding-right: 0;
}

/* Unique Footer Styles */
ul.foot{
	display: inline-block;
	text-align: center;
	padding: 8px 0;
	border-bottom: 1px solid #fff;
}

 ul.foot>li{
	font-size: 11px;
}

ul.foot>li:first-of-type{
	border-right: 1px solid #ffffff;
}


@media only screen and (min-width: 771px){
	ul.foot{
		display: none;
	}
}



</style>


