<template>
  <div class="container" style="background-color: #fff;">
    <h1>No No No</h1>
    <p>This is bad Pathway.. Rerouting now</p>
  </div>
</template>

<script>
export default{
	mounted(){
		const self = this;
		setTimeout(()=>{ self.$router.push({name: 'Index'})}, 3000)
	}
}
</script>

<style>

