<template>
	<div class="single-dealer">
		<div class="sd-about column-flex">
			<div class="sd-header">
				{{ dealer.name }}
			</div>
			<div class="sd-contact column-flex">
				<a :href="`tel:${dealer.phone1}`" class="desktop-phone">
					<img class="icon-lg" src="../assets/images/phone-icon-desktop.png" /> <span>{{ dealer.phone1.replace(/[-]/g, '.') }}</span>
				</a>
				<a :href="`tel:${dealer.phone1}`" class="mobile-phone secondary-blue-bg">
					<img class="icon-lg" src="../assets/images/phone-icon-mobile.png" /> Tap to call <span class="mobile-number">{{ dealer.phone1.replace(/[-]/g, '.') }}</span>
				</a>
				<p>Can't talk now? Click below to send an email.</p>
				<a href="javascript:;" @click="$emit('openModal', dealer)">
					<img class="icon-md" src="../assets/images/email-icon.png" /> <span>Contact this Pro</span>
				</a>
			</div>
			<div class="sd-hours">
				<p>
					<span>Business Hours</span><br />
					Weekdays: {{ dealer.weekHours.mon }} <br />
					Saturdays: <span v-if="dealer.weekHours.sat">{{dealer.weekHours.sat}}</span><span v-else>Closed</span> <br />
					Sundays: <span v-if="dealer.weekHours.sun">{{dealer.weekHours.sun}}</span><span v-else>Closed</span>
				</p>
			</div>
		</div>
		<div class="sd-footer bg-gray">
			<div v-for="(cert, x) in dealer.certifications" :key="x" class="certification">
				<i :class="certIcons[cert]"></i> {{cert}}
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return {
				certIcons:{
					'Installation Pro': 'ss-star',
					'Service Pro': 'ss-settings',
					'Residential Pro': 'ss-home',
					'Commercial Pro': 'ss-users'
				}
			}
		},
		props: ['dealer']
	}
</script>

<style scoped>

.column-flex{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-flex: 1;
	    -ms-flex-positive: 1;
	        flex-grow: 1;
}

div.single-dealer{
	position: relative;
	width: 100%;
	min-height: 350px;
	margin-bottom: 0;
	border-right: 1px solid #cccccc;
	border-left: 1px solid #cccc;
	-webkit-box-shadow: 0 3px 1px #757575;
	        box-shadow: 0 3px 1px #757575;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

div.single-dealer>div.sd-about{
	padding: 10px;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

div.sd-about .sd-header{
	font-family: 'open-sans-light';
	font-size: 1.3rem;
	width: 100%;
	min-height: 45px;
	margin: 0;
	padding-bottom: 0;
	border: none;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}


div.sd-contact{
	padding: 15px 0;
	max-height: 100px;
}

div.sd-contact a.mobile-phone{
	padding: 3px 5px;
	font-family: 'open-sans-bold';
	font-size: 0.6rem;
	display: block;
	-webkit-transition: background-color .25s linear;
	-o-transition: background-color .25s linear;
	transition: background-color .25s linear;
}

div.sd-contact a.mobile-phone:hover{
	background-color: #044696;
}

div.sd-contact a.mobile-phone span{
	font-size: 0.9rem;
}

div.sd-contact a.mobile-phone img{
	vertical-align: middle;
	margin-right: 5px;
}

div.sd-contact a.desktop-phone{
	font-family: 'open-sans-extra-bold';
	color: #000000;
	font-size: 1.35rem;
	display: none;
}

div.sd-contact>p{
	font-family: 'open-sans-italic';
	font-size: 11px;
	color: #757575;
}

div.sd-contact>a:last-of-type{
	font-family: 'open-sans-bold';
	color: #044696;
	border: 1px solid #5fb6b9;
	background-color: #ffffff;
	border-radius: 3px;
	padding: 8px;
	-webkit-transition: background-color .25s linear;
	-o-transition: background-color .25s linear;
	transition: background-color .25s linear;
}
div.sd-contact>a:last-of-type:hover{
	background-color: #d2ffff;
}
div.sd-contact>a span{
	margin-left: 10px;
	font-size: 0.9em;
}

div.sd-contact>a.mobile-phone span{
	margin-left: 0;
}

div.sd-hours>p{
	font-size: 12px;
	line-height: 1.5;
}

div.sd-hours>p>span:first-of-type{
	font-family: 'open-sans-bold';
}

div.sd-footer{
	position: absolute;
	bottom: 0;
	right: 0;
	left: 0;
	padding: 10px 25px;
	border-radius: 0 0 8px 8px;
	min-height: 35px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
}

div.sd-footer .certification{
	font-size: 11px;
	width: 40%;
	text-align: left;
	margin-bottom: 5px;
}

.certification i{
	margin-right: 5px;
}

@media only screen and (min-width: 700px){
	div.single-dealer{
		width: 48%;
	}
	div.single-dealer:not(:nth-child(2n)){
		margin-right: 4%;
	}
	div.sd-about .sd-header{
		border-bottom: 1px solid #757575;
		padding-bottom: 15px;
	}
	div.sd-contact a.mobile-phone{
		display: none;
	}
	div.sd-contact a.desktop-phone{
		display: block;
	}
	div.single-dealer{
		margin-bottom: 15px;
		border: 1px solid #cccccc;
		border-radius: 8px;
		-webkit-box-shadow: 1px 3px 1px #757575;
		        box-shadow: 1px 3px 1px #757575;
	}
}

@media only screen and (min-width: 1000px){
	div.single-dealer{
		width: 32%;
	}
	div.single-dealer:not(:nth-child(2n)){
		margin-right: 0;
	}
	div.single-dealer:not(:nth-child(3n)){
		margin-right: 2%;
	}
}

@media (min-width: 700px) and (max-width: 769px){
	div.sd-footer{
		padding: 10px;
	}
	div.sd-footer .certification{
		width: 49%;
	}
}

</style>



