<template>
	<div id="mobile-menu">
		<div class="mobile-nav-head">
			<div class="close-mobile-menu ss-delete" @click="$emit('toggleMobileMenu')"></div>
			<h5>Menu</h5>
		</div>
		<div class="mobile-links">
			<router-link class="mobile-link" 
									 tag="div"
									 v-for="(link, x) in links"
									 :key="x" :to="{name: link.routeName}"
									 @click.native="$emit('toggleMobileMenu')">
				<p><span>{{ link.text }}</span></p>
				<img class="icon-md" src="../../assets/images/next-arrow.png" :title="`Go to ${link.text}`" />
			</router-link>
		</div>
	</div>
</template>

<script>
	import TopNav from './TopNav';
	import MainNav from './MainNav';

	export default {
		data(){
			return{
				links:[
				{text: 'Pools & Spas', routeName: 'PoolsAndSpas'},
				{text: 'Supplies', routeName: 'Supplies'},
				{text: 'Resources', routeName: 'Resources'},
				{text: 'Services', routeName: 'Services'},
				]
			}
		},
		components:{
			appTopNav: TopNav,
			appMainNav: MainNav
		}
	}
</script>

<style scoped>
#mobile-menu{
	font-family: 'open-sans-bold';
	border-top: 4px solid #5fb6b9;
	border-bottom: 4px solid #5fb6b9;
	background-color: #fff;
	width: 100%;
	display: block;
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	z-index: 999;
}
#mobile-menu>div{
	width: 100%;
}
.mobile-nav-head{
	position: relative;
}
.mobile-nav-head h5{
	font-size: 1.15rem;
	text-transform: uppercase;
	color: #044696;
	padding: 30px 0;
	margin: 0;
}
.mobile-nav-head .close-mobile-menu{
	text-align: center;
	color: #5fb6b9;
	padding: 15px;
	cursor: pointer;
	position: absolute;
	top: 0;
	right: 0;
	-webkit-transform: rotate(0deg);
	    -ms-transform: rotate(0deg);
	        transform: rotate(0deg);
	-webkit-transform-origin: 48% 46%;
	    -ms-transform-origin: 48% 46%;
	        transform-origin: 48% 46%;
	-webkit-transition: -webkit-transform .4s linear;
	transition: -webkit-transform .4s linear;
	-o-transition: transform .4s linear;
	transition: transform .4s linear;
	transition: transform .4s linear, -webkit-transform .4s linear;
}
.mobile-nav-head .close-mobile-menu:hover{
	-webkit-transform: rotate(180deg);
	    -ms-transform: rotate(180deg);
	        transform: rotate(180deg);
}

.mobile-links{
	width: 100%;
}

.mobile-links .mobile-link{
	padding: 25px 15px;
	background-color: #f7f6f7;
	color: #757575;
	cursor: pointer;
	border-bottom: 2px solid #ffffff;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-transition: background-color .2s linear;
	-o-transition: background-color .2s linear;
	transition: background-color .2s linear;
}

.mobile-links .mobile-link:last-of-type{
	border: none;
}

.mobile-links .mobile-link:hover{
	background-color: #defdff;
}

.mobile-link p{
	display: inline-block;
	margin: 0;
}

@media only screen and (min-width: 1000px){
	#mobile-menu{
		display: none;
	}
}

</style>


