<template>
	<div id="footer">
		<div class="primary-blue-bg" id="main-footer">
			<img src="../../assets/images/pool-pros-logo-footer.png" alt="Pool Pros" />
			<div>
				Connect With Us <span class="social-media">
					<img class="icon-md" src="../../assets/images/twitter-icon.png" alt="Twitter" />
					<img class="icon-sm" src="../../assets/images/facebook-icon.png" alt="Facebook" />
					<img class="icon-md" src="../../assets/images/youtube-icon.png" alt="YouTube" />
				</span>
			</div>
		</div>
		<div class="secondary-blue-bg" id="sub-footer">
			<app-com-service spot="foot"></app-com-service>
			<ul class="bottom-links">
				<li>© {{ year }} Pool Pros</li>
				<li><a href="javascript:;" @click="false">Privacy Policy</a></li>
				<li><a href="javascript:;" @click="false">Terms and Conditions</a></li>
			</ul>
		</div>
	</div>
</template>

<script>
	import ComService from './ComService';

	export default {
		data(){
			return{
				year: null
			}
		},
		components:{
			appComService: ComService
		},
		mounted(){
			this.year = (new Date()).getFullYear()
		}
	}
</script>

<style scoped>
#main-footer{
	padding: 20px 0;
}

#main-footer>img{
	max-width: 150px;
}

#main-footer>div{
	font-family: 'open-sans-bold';
	text-transform: uppercase;
	font-size: 0.9rem;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	max-width: 300px;
	margin: 15px auto 0;
}
span.social-media img{
	padding-left: 10px;
	cursor: pointer;
	-webkit-transform: scale(1);
	    -ms-transform: scale(1);
	        transform: scale(1);
	-webkit-transition: -webkit-transform .2s linear;
	transition: -webkit-transform .2s linear;
	-o-transition: transform .2s linear;
	transition: transform .2s linear;
	transition: transform .2s linear, -webkit-transform .2s linear;
}
span.social-media img:nth-of-type(2){
	max-width: 7px;
}

span.social-media img:hover{
	-webkit-transform: scale(1.25);
	    -ms-transform: scale(1.25);
	        transform: scale(1.25);
}

ul.bottom-links{
	list-style: none;
	display: block;
	margin: 0;
	padding: 8px 0;
}
ul.bottom-links>li{
	display: inline;
	color: #ffffff;
	font-size: 11px;
	padding: 0 10px;
}
ul.bottom-links>li:not(:last-child){
	border-right: 1px solid #ffffff;
}

ul.bottom-links>li>a{
	color: #ffffff;
	-webkit-transition: color .2s linear;
	-o-transition: color .2s linear;
	transition: color .2s linear;
}

ul.bottom-links>li>a:hover{
	color: #cccccc;
}

@media only screen and (min-width: 700px){
	#main-footer>div{
		display: block;
	}
}
</style>



