<template>
	<div class="containerfluid primary-blue-bg">
		<div class="container">
			<div class="top-nav">
				<app-com-service spot="head"></app-com-service>
			</div>
		</div>
	</div>
</template>

<script>
	import ComService from './ComService';

	export default {
		components:{
			appComService: ComService
		}
	}
</script>

<style scoped>
.top-nav{
	width: 100%;
	height: 35px;
	margin: 0 auto;
	display: none;
}

@media only screen and (min-width: 770px){
	.top-nav{
		display: table;
	}
}

</style>


