<template>
	<div class="container-fluid">
		<div id="dealer-filter">
			<div class="filter-mobile">
				<div class="container plr0 filter-mobile-top">
					<div class="dealer-count">{{dealersTotal}} dealers in {{dealers.length}}</div>
					<div>Filter Results</div>
					<div @click="showMobileFilters = !showMobileFilters"><i class="ss-dropdown"></i></div>
				</div>
				<transition name="fade" mode="out-in">
					<div id="mobile-filters" class="container plr0" v-show="showMobileFilters">
						<app-dealer-filters :dealersFilters="dealersFilters" v-on:changeFilters="changeFilters"></app-dealer-filters>
					</div>
				</transition>
			</div>
			<div class="filter-desktop container">
				<div>
					<div class="dealer-count">{{dealersTotal}} dealers in {{dealers.length}}</div>
					<div>Filter Results</div>
					<app-dealer-filters :dealersFilters="dealersFilters" v-on:changeFilters="changeFilters"></app-dealer-filters>
				</div>
			</div>
		</div>
		<div class="container">
			<div id="all-dealers">
				<app-single-dealer v-for="dealer in dealersFiltered" :key="dealer.data.companyID" :dealer="dealer.data" v-on:openModal="openModal"></app-single-dealer>
			</div>
		</div>
		<app-modal v-if="showModal" :dealerData="modalData" v-on:close="closeModal"></app-modal>
	</div>
</template>

<script>
	import DealerData from '../../static/dealers.json';
	import SingleDealer from './SingleDealer';
	import DealerFilters from './DealerFilters';
	import Modal from './universal/Modal';

	export default {
		data () {
			return {
				dealers: DealerData.dealers,
				dealersFiltered: DealerData.dealers,
				dealersTotal: DealerData.dealers.length,
				dealersFilters: {
					service: false,
					installation: false,
					residential: false,
					commercial: false
				},
				showMobileFilters: false,
				showModal: false,
				modalData: null
			}
		},
		components:{
			appSingleDealer: SingleDealer,
			appDealerFilters: DealerFilters,
			appModal: Modal
		},
		mounted(){
			console.log(this.dealers);
			this.findFilterMatches();
		},
		methods:{
			findFilterMatches(){
				let filters = this.dealersFilters;
				this.dealersFiltered = this.dealers.filter((dealer, index)=>{
					let match = true;
					let dealerCerts = dealer.data.certifications.join('').split('Pro').join(' ').toLowerCase();
					for(let filter in filters){
						if(filters[filter]){
							if(!dealerCerts.includes(filter)){
								match = false;
							}
						}
					}
					return match;
				})
				this.dealersTotal = this.dealersFiltered.length
			},
			changeFilters(filters){
				console.log
				this.dealersFilters = filters;
				this.findFilterMatches();
			},
			openModal(data){
				this.showModal = true;
				this.modalData = data;
			},
			closeModal(){
				this.showModal = false;
				this.modalData = null;
			}
		}
	}
</script>

<style scoped>

#dealer-filter>div{
	background-color: #f7f6f7;
	font-family: 'open-sans-bold';
	font-size: 0.9rem;
}

.dealer-count{
	color: #2678de;
}

.plr0{
	padding-left: 0;
	padding-right: 0;
}
/*--Mobile --*/
#dealer-filter>div.filter-mobile{
	display: block;
	padding-bottom: 15px;
	padding-top: 15px;
	border-bottom: 1px solid #aaa;
}

div.filter-mobile div.filter-mobile-top{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	border-bottom: 1px solid #aaa;
}

div.filter-mobile div.filter-mobile-top>div{
	padding: 15px 5px;
	display: inline-block;
	border-right: 1px solid #aaa;
}

div.filter-mobile div.filter-mobile-top>div:not(:first-of-type){
	border-top: 1px solid #aaa;
}

div.filter-mobile div.filter-mobile-top>div:first-of-type{
	width: 47%;
}

div.filter-mobile div.filter-mobile-top>div:nth-of-type(2){
	background-color: #ffffff;
	width: 43%;
}

div.filter-mobile div.filter-mobile-top>div:last-of-type{
	width: 10%;
	cursor: pointer;
}

#mobile-filters{
	background-color: #fff;
	border-right: 1px solid #aaa;
	border-bottom: 1px solid #aaa;
	border-left: 1px solid #aaa;
}

.fade-enter-active, .fade-leave-active {
  -webkit-transition-property: opacity;
  -o-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: .25s;
       -o-transition-duration: .25s;
          transition-duration: .25s;
}

.fade-enter-active {
  -webkit-transition-delay: .25s;
       -o-transition-delay: .25s;
          transition-delay: .25s;
}

.fade-enter, .fade-leave-active {
  opacity: 0
}


/*--Desktop--*/
#dealer-filter>div.filter-desktop.container{
	display: none;
	margin-bottom: 15px;
}
.filter-desktop>div{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}

.filter-desktop>div>div{
	padding: 10px 0;
}

.filter-desktop>div>div:first-of-type{
	width: 15%;
	border-right: 1px solid #ccc;
	padding-right: 10px;
}

.filter-desktop>div>div:nth-of-type(2){
	width: 15%;
}

.filter-desktop>div>div:last-of-type{
	width: 45%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	-webkit-box-pack: start;
	    -ms-flex-pack: start;
	        justify-content: flex-start;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}


/*-- All Deal Boxes --*/
#all-dealers{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-webkit-box-pack: start;
	    -ms-flex-pack: start;
	        justify-content: flex-start;
	-webkit-box-align: start;
	    -ms-flex-align: start;
	        align-items: flex-start;
}

/*-- Media Queries --*/
@media only screen and (min-width: 700px){
	#dealer-filter>div.filter-mobile{
		margin-bottom: 15px;
	}
}

@media only screen and (min-width: 1000px){
	#dealer-filter>div.filter-desktop.container{
		padding: 0;
		display: block;
	}
	#dealer-filter>div.filter-mobile{
		padding: 0;
		display: none;
	}
}

</style>



