<template>
	<div class="containerfluid" id="main-nav">
		<div class="container">
			<div class="navbar navflex">
				<div class="navsection navleft">
					<div class="navbrand">
						<router-link tag="a" :to="{name: 'Index'}">
							<img class="img-resp" src="../../assets/images/pool-pros-logo.png" alt="Pool Pros" />
						</router-link>
					</div>
				</div>
				<div class="navsection navmiddle navflex primary-blue-txt">
					<div class="navlink">
						<router-link tag="span" :to="{name: 'PoolsAndSpas'}">
							Pools &amp; Spas
						</router-link>
					</div>
					<div class="navlink">
						<router-link tag="span" :to="{name: 'Supplies'}">
							Supplies
						</router-link>
					</div>
					<div class="navlink">
						<router-link tag="span" :to="{name: 'Resources'}">
							Resources
						</router-link>
					</div>
					<div class="navlink">
						<router-link tag="span" :to="{name: 'Services'}">
							Services
						</router-link>
					</div>
					<div class="navlink navlink-bttn">
						<div>
							<router-link tag="span" :to="{name: 'FindAPool'}">
								<img class="icon-sm" 
								src="../../assets/images/location-icon.png" 
								title="Find a Pool" />
							</router-link>
						</div>
					</div>
				</div>
				<div class="navsection navright navmobile">
					<img class="mobile-menu-icon"
					src="../../assets/images/menu-icon-mobile.png"
					title="Nav Dropdown"
					@click="$emit('toggleMobileMenu')" />
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data () {
			return {
				page: 'Pool Pros'
			}
		},
	}
</script>

<style scoped>
#main-nav{
	background-color: #ffffff;
	-webkit-box-shadow: 0 3px 2px #aaa;
	box-shadow: 0 3px 2px #aaa;
}

.navbar{
	width: 100%;
	padding: 5px 0 0 0;
}

.navflex{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	-ms-flex-direction: row;
	flex-direction: row;
	-webkit-box-pack: justify;
	-ms-flex-pack: justify;
	justify-content: space-between;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
}

.navlink{
	font-family: 'open-sans-bold';
	font-size: 0.85rem;
	display: none;
	padding: 5px 20px;
	cursor: pointer;
	-webkit-transition: color .2s linear;
	-o-transition: color .2s linear;
	transition: color .2s linear;
}

.navlink:last-of-type{
	padding-left: 20px;
	padding-right: 0;
}

.navlink:hover{
	color: #2678de;
}

.navlink-bttn>div{
	display: inline-block;
	border: 1px solid #2678de;
	border-radius: 3px;
	padding: 6px;
	color: #2678de;
	font-size: 0.85rem;
	-webkit-transition: background-color .2s linear;
	-o-transition: background-color .2s linear;
	transition: background-color .2s linear;
}

.navlink-bttn>div:hover{
	background-color: #e7e7e7;
}

.navlink-bttn span::after{
	content: ' Find a Pro';
}

.navbrand{
	max-width: 125px;
}

.mobile-menu-icon{
	cursor: pointer;
	max-width: 53px;
	-webkit-transform: scale(1);
	-ms-transform: scale(1);
	transform: scale(1);
	-webkit-transition: -webkit-transform .2s ease-in-out;
	transition: -webkit-transform .2s ease-in-out;
	-o-transition: transform .2s ease-in-out;
	transition: transform .2s ease-in-out;
	transition: transform .2s ease-in-out, -webkit-transform .2s ease-in-out;
}
.mobile-menu-icon:hover{
	-webkit-transform: scale(1.05);
	-ms-transform: scale(1.05);
	transform: scale(1.05);
}



@media only screen and (min-width: 770px){
	.navbrand{
		max-width: 200px;
	}
	.navlink-bttn>div{
		padding: 8px 12px;
	}
	.navlink-bttn>div{
		font-size: 1rem;
	}
}

@media only screen and (max-width: 999px){
	div.navleft{
		width: 37%;
	}
	div.navmiddle{
		width: 50%;
	}
	div.navright{
		width: 13%;
		position: relative;
	}
	div.navright.navmobile img{
		position: absolute;
		top: -23px;
		left: 0;
	}
	div.navsection:nth-of-type(2){
		text-align: right;
	}
	div.navlink.navlink-bttn{
		display: inline-block;
		width: 100%;
		padding-right: 10px;
	}
	div.navlink-bttn span{
		font-family: 'open-sans-extra-bold';
		text-transform: uppercase;
	}
}

@media only screen and (min-width: 1000px){
	div.navbar{
		padding: 15px 0;
	}
	div.navlink{
		display: inline-block;
	}
	div.navlink.navlink-bttn span::after{
		content: ' Find a Pool Pro';
	}

	div.navleft{
		width: auto;
	}
	div.navmiddle{
		width: auto;
	}

	div.navright.navmobile{
		width: 0%;
		overflow: none;
		display: none;
	}
}

</style>







