<template>
	<div>
		<div class="single-filter">
			<div :class="{'checkbox': true, 'checkbox-on': dealersFilters.service, 'checkbox-off': !dealersFilters.service}"
			@click="filterSelect('service')"></div> Service
		</div>
		<div class="single-filter">
			<div :class="{'checkbox': true, 'checkbox-on': dealersFilters.installation, 'checkbox-off': !dealersFilters.installation}"
			@click="filterSelect('installation')"></div> Installation
		</div>
		<div class="single-filter">
			<div :class="{'checkbox': true, 'checkbox-on': dealersFilters.residential, 'checkbox-off': !dealersFilters.residential}"
			@click="filterSelect('residential')"></div> Residential
		</div>
		<div class="single-filter">
			<div :class="{'checkbox': true, 'checkbox-on': dealersFilters.commercial, 'checkbox-off': !dealersFilters.commercial}"
			@click="filterSelect('commercial')"></div> Commercial <img class="icon-md" src="../assets/images/tool-tip-icon-filtering.png" />
		</div>
	</div>
</template>

<script>
	import DealerData from '../../static/dealers.json';
	import SingleDealer from './SingleDealer';

	export default {
		props: ['dealersFilters'],
		components:{
			appSingleDealer: SingleDealer
		},
		methods:{
			filterSelect(type){
				this.dealersFilters[type] = !this.dealersFilters[type];
				this.$emit('changeFilters', this.dealersFilters);
			}
		}
	}
</script>

<style scoped>

#mobile-filters>div{
	padding: 15px;
	width: 100%;
	text-align: left;
}

.single-filter{
	font-family: 'open-sans-regular';
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	-webkit-box-pack: start;
	    -ms-flex-pack: start;
	        justify-content: flex-start;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}

.single-filter img{
	margin-left: 10px;
}

.checkbox{
	display: inline-block;
	width: 18px;
	height: 18px;
	margin: 7px;
	border-radius: 3px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	cursor: pointer;
}

.checkbox-off{
	border: 1px solid #aaa;
}

.checkbox-on{
	border: 6px solid #2678de;
}


</style>



